$prefix: '.rise-blocks';
#{$prefix}-accordion-title {
    background: #eee;
    padding: 10px;
    border: 1px solid #d7d7d7;
}

.wp-block-rise-blocks-accordion-item > button {
    position: absolute;
    top: 12px;
    right: 20px;
    background: black;
    border: none;
    padding: 0 14px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    cursor: pointer;
    text-transform: capitalize;
    color: #fff;
    border-radius: 3px;
    font-size: 12px;
}